<template>
  <div class="full-width">
    <!-- No Answer -->
    <div
      v-if="!answer || answer?.length < 1"
      class="no-answer text-primary-400"
    >
      -
    </div>

    <!-- textarea1 -->
    <div
      v-else-if="qtype === 'textarea1'"
      class="raw-html"
      :class="{
        ['qt-textarea1']: qtype === 'textarea1',
      }"
      v-html="answer"
    ></div>

    <!-- textarea2 -->
    <div
      v-else-if="qtype === 'textarea2'"
      class="raw-html"
      v-html="answer"
      :class="{
        ['qt-textarea2']: qtype === 'textarea2',
      }"
    ></div>

    <!-- textfield -->
    <div
      v-else-if="qtype === 'textfield'"
      :class="{
        ['qt-textarea2']: qtype === 'textarea2',
      }"
    >
      {{ answer }}
    </div>

    <!-- select || multiple -->
    <template v-else-if="qtype === 'select_multiple' || qtype === 'select'">
      <div
        v-for="ans in Array.isArray(answer) ? answer : [answer]"
        :key="ans"
        :class="{
          ['qt-select_multiple']: qtype === 'select_multiple',
          ['qt-select']: qtype === 'select',
        }"
      >
        {{
          checkIfObject(ans)
            ? ans?.label || ans?.value
            : filterOptionValue(ans)?.label || ans
        }}
      </div>
    </template>

    <!-- radio -->
    <div
      v-else-if="qtype === 'radio'"
      :class="{
        ['qt-radio']: qtype === 'radio',
      }"
    >
      {{
        checkIfObject(answer)
          ? answer?.label || answer?.value
          : filterOptionValue(answer)?.label || answer
      }}
    </div>

    <!-- checkbox -->
    <template v-else-if="qtype === 'checkbox'">
      <div
        v-for="ans in Array.isArray(answer) ? answer : [answer]"
        :key="ans"
        :class="{
          ['qt-checkbox']: qtype === 'checkbox',
        }"
      >
        {{
          checkIfObject(ans)
            ? ans?.label || ans?.value
            : filterOptionValue(ans)?.label || ans
        }}
      </div>
    </template>

    <!-- select_platform -->
    <template v-else-if="qtype === 'select_platform'">
      <div
        :class="{
          ['qt-select_platform']: qtype === 'select_platform',
        }"
      >
        <a :href="platforms[answer]?.url" target="_blank">
          <q-badge
            class="badge badge-blue full-height"
            v-if="platforms?.[answer]"
          >
            <div class="q-px-sm q-py-xs">
              <q-icon
                :name="`img:${platforms[answer]?.icon}`"
                size="md"
                class="q-pr-sm"
              />
              <span>{{ platforms[answer]?.title }}</span>
            </div>
          </q-badge>
          <div v-else>
            {{ typeof answer === 'string' ? capitalize(answer) : answer }}
          </div>
        </a>
      </div>
    </template>

    <!-- video_walkthrough -->
    <template v-else-if="qtype === 'video_walkthrough'">
      <q-video
        :ratio="16 / 9"
        class="rounded fit"
        :src="urlShareToEmbed(answer)"
        :class="{
          ['qt-video_walkthrough']: qtype === 'video_walkthrough',
        }"
      />
    </template>

    <!-- select_brand -->
    <div
      v-else-if="qtype === 'select_brand'"
      :class="{
        ['qt-select_brand']: qtype === 'select_brand',
      }"
    >
      {{ selectedOrgBrands[answer]?.brand_name }}
    </div>

    <!-- select_taskcategory -->
    <div
      v-else-if="qtype === 'select_taskcategory'"
      :class="{
        ['qt-select_taskcategory']: qtype === 'select_taskcategory',
      }"
    >
      {{ answer }}
    </div>

    <!-- select_projectcategory -->
    <div
      v-else-if="qtype === 'select_projectcategory'"
      :class="{
        ['qt-select_projectcategory']: qtype === 'select_projectcategory',
      }"
    >
      {{ answer }}
    </div>

    <!-- select_tasktype -->
    <div
      v-else-if="qtype === 'select_tasktype'"
      :class="{
        ['qt-select_tasktype']: qtype === 'select_tasktype',
      }"
    >
      {{ taskDirectory[answer]?.title }}
    </div>

    <!-- select_projecttype -->
    <div
      v-else-if="qtype === 'select_projecttype'"
      :class="{
        ['qt-select_projecttype']: qtype === 'select_projecttype',
      }"
    >
      {{ projectDirectory[answer]?.title }}
    </div>

    <!-- url -->
    <div
      v-else-if="qtype === 'url'"
      :class="{
        ['qt-url']: qtype === 'url',
      }"
    >
      <a v-if="answer" :href="`//${answer}`" target="_blank"
        >https://{{ answer }}</a
      >
    </div>

    <!-- phone -->
    <div
      v-else-if="qtype === 'phone'"
      :class="{
        ['qt-phone']: qtype === 'phone',
      }"
    >
      <a v-if="answer" :href="`tel:${formatPhone(answer)}`">{{
        formatPhone(answer)
      }}</a>
    </div>

    <!-- upload_single' || qtype === 'upload_multiple -->
    <template
      v-else-if="qtype === 'upload_single' || qtype === 'upload_multiple'"
    >
      <div
        class="row items-center q-col-gutter-lg"
        :class="{
          ['qt-upload_single']: qtype === 'upload_single',
          ['qt-upload_multiple']: qtype === 'upload_multiple',
        }"
      >
        <div
          v-for="ans in Array.isArray(answer) ? answer : [answer]"
          :key="ans"
          class="col-12"
          :class="{
            ['col-sm-6']: answer.length > 1,
          }"
        >
          <ImageLoader
            :ratio="qtype === 'upload_single' ? undefined : 1"
            v-if="
              checkIfObject(ans) &&
              checkFileExtensions(ans.name, $config.supportedDisplayImage)
            "
            :file="ans"
          />
          <a
            v-else-if="checkIfObject(ans)"
            target="_blank"
            class="row items-center q-col-gutter-sm cursor-pointer text-primary"
            :href="ans.download_url"
          >
            <div class="col-3">
              <CardIconBox :icon="getFileTypeIcon(ans.name)" />
            </div>
            <div
              class="hover-text-secondary overflow-hidden"
              style="text-overflow: ellipsis"
            >
              {{ ans.name }}
            </div>
          </a>
          <div v-else>File: {{ ans }}</div>
        </div>
      </div>
    </template>

    <!-- website_content -->
    <template v-else-if="qtype === 'website_content'">
      <div
        :class="{
          ['qt-website_content']: qtype === 'website_content',
        }"
      >
        <div
          v-if="answer?.info"
          :class="{ ['q-pb-md']: answer.uploads?.length }"
        >
          {{ answer.info }}
        </div>
        <template v-if="answer.uploads?.length">
          <div class="row items-center q-col-gutter-lg">
            <div
              v-for="ans in answer.uploads || []"
              :key="ans"
              class="col-12"
              :class="{
                ['col-sm-6']: answer.uploads?.length > 1,
              }"
            >
              <ImageLoader
                :ratio="answer.uploads?.length === 1 ? undefined : 1"
                v-if="
                  checkIfObject(ans) &&
                  checkFileExtensions(ans.name, $config.supportedDisplayImage)
                "
                :file="ans"
              />
              <a
                v-else-if="checkIfObject(ans)"
                target="_blank"
                class="row items-center q-col-gutter-sm cursor-pointer text-primary"
                :href="ans.download_url"
              >
                <div class="col-3">
                  <CardIconBox :icon="getFileTypeIcon(ans.name)" />
                </div>
                <div
                  class="hover-text-secondary overflow-hidden"
                  style="text-overflow: ellipsis"
                >
                  {{ ans.name }}
                </div>
              </a>
              <div v-else>File: {{ ans }}</div>
            </div>
          </div>
        </template>
      </div>
    </template>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import {
  checkIfObject,
  urlShareToEmbed,
  checkFileExtensions,
  getFileTypeIcon,
} from 'src/assets/scripts/functions';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import { format } from 'quasar';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';
import ImageLoader from 'src/components/Helpers/ImageLoader.vue';

export default defineComponent({
  name: 'DynamicAnswers',

  props: {
    qid: {
      type: Number,
      default: 0,
      required: true,
    },
    qtype: {
      type: String,
      default: '',
    },
    answer: {
      default: null,
    },
    aqtype: {
      type: String,
      default: '',
    },
    question: {
      type: Object,
      default: () => {},
    },
  },

  components: { ImageLoader, CardIconBox },

  data() {
    return {
      checkIfObject,
      urlShareToEmbed,
      capitalize: format.capitalize,
      checkFileExtensions,
      getFileTypeIcon,
    };
  },

  computed: {
    ...mapState(useGrowmodo, [
      'platforms',
      'taskDirectory',
      'projectDirectory',
    ]),
    ...mapState(useUser, ['selectedOrgBrands']),
  },

  methods: {
    filterOptionValue(ans) {
      if (!this.question.options?.length) return;
      let val = this.question.options.filter((e) => e.value === ans);
      if (!val?.length)
        val = this.question.options.filter((e) => e.label === ans);
      return val?.length ? val[0] : val;
    },
    formatPhone(ans) {
      if (!ans) return '';
      const ar = ans.split('+');
      return `+${ar[0]} ${ar[1]}`;
    },
  },
});
</script>
